.heading-body{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    margin-bottom: 20px;
}
.heading-card{
    position: relative;
    width: 450px;
    height: 250px;
    background: orange;
    border-radius: 20px;
    display: flex;
    align-items: center;
    border-radius: 20px;
    transition: 0.5s;
}
.heading-card .circle{
    position:absolute;
    top: 0;
    left: 0;
    left:0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    overflow: hidden;
}
.heading-card .circle::before{
    content: " ";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: blue;
    -webkit-clip-path: circle(120px at center);
            clip-path: circle(120px at center);
    transition: 0.7s;
}
.heading-card:hover .circle:before{
    background: yellow;
    -webkit-clip-path: circle(400px at center);
            clip-path: circle(400px at center);
}
.heading-card img{
    position:absolute;
    top:50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    height: 150px;
    border-radius: 10px;
}
.heading-card:hover img{
    left :72%;
    height: 230px;
}
.heading-card .heading-content{
    position:relative;
    width: 50%;
    left: 20%;
    color: darkgreen;
    padding: 20px 20px 20px 40px;
    transition: 0.5s;
    opacity:0;
    visibility: hidden;
}
.heading-card:hover .heading-content{
    left:0;
    opacity:1;
    visibility: visible;
}

