  /* Media Query for Tablets Ipads portrait mode */
   @media (min-width: 768px) and (max-width: 1024px){
    .heading-card .heading-content{
        position:relative;
        width: 50%;
        left: 20%;
        color: darkgreen;
        padding: 20px 20px 20px 40px;
        transition: 0.5s;
        opacity:0;
        visibility: hidden;
    }
  }  
  
